.Background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  // height: auto;
  background-color: white;
  //   background-image: url("./img/12.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -10;
}

.pageContent {
  position: relative;
  width: 100%;
  min-height: 100vh;
  height: auto;
  overflow: auto;
  z-index: 0;
}
.contrast-background-white {
  background-color: black;
}
