.PanelBlock {
    width: 85%;
    margin: 10px auto;
    padding: 10px 5%;
    background-color: rgba(0, 0, 0, 0.15);
    border: 1px solid white;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
}