.RadioBtn {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Hide the browser's default radio button */
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
  }

  /* On mouse-over, add a grey background color */
  &:hover input ~ .checkmark {
    background-color: #ccc;
  }

  /* When the radio button is checked, add a blue background */
  input:checked ~ .checkmark {
    background-color: #54a335;
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the indicator (dot/circle) */
  .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
  }
}

.contrast-black-radio {
    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 25px;
      width: 25px;
      background-color: black;
      border-radius: 50%;
      border: 2px solid white;
    }
    input:checked ~ .checkmark {
      background-color: white !important;
      border: none;
    }
    .checkmark:after {
      background: rgb(0, 0, 0);
    }
    &:hover input ~ .checkmark {
      background-color: rgb(192, 192, 166);
    }
  
  }

.contrast-yellow-radio {
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: black;
    border-radius: 50%;
    border: 2px solid yellow;
  }
  input:checked ~ .checkmark {
    background-color: yellow !important;
    border: none;
  }
  .checkmark:after {
    background: rgb(0, 0, 0);
    // border: -2px solid yellow;
  }
  &:hover input ~ .checkmark {
    background-color: rgb(122, 122, 66);
  }

}
